namespace $ {
	$mol_test({
		
		'Word making'() {
			
			$mol_assert_ok( $mol_spell_ru.test( '' ) )
			$mol_assert_ok( $mol_spell_ru.test( '' ) )
			$mol_assert_ok( $mol_spell_ru.test( '' ) )
			$mol_assert_ok( $mol_spell_ru.test( '' ) )
			$mol_assert_ok( $mol_spell_ru.test( '' ) )
			$mol_assert_ok( $mol_spell_ru.test( '' ) )
			$mol_assert_ok( $mol_spell_ru.test( '' ) )
			
		},
		
		'Wrong words'() {
			
			$mol_assert_not( $mol_spell_ru.test( '' ) )
			$mol_assert_not( $mol_spell_ru.test( 'book' ) )
			
		},
		
	})
}
